.App {
  height: 100vh;
  max-width: 100vw;
  --border-bottom: 4px solid #E0354B;
	--padding: 1.2em;
	--heading-font-size: 1.4rem;
	--gen-font-size: 1rem;
	font-family: Arial, Helvetica, sans-serif;
}

.App-header {
	font-size: var(--heading-font-size);
	color: #E0354B;
	display: flex;
	align-items: center;
	padding: var(--padding);
	border-bottom: var(--border-bottom);
}

.App-header img {
	width: 250px;
	height: 250px;
}

.App-body {
	font-size: var(--gen-font-size);
	padding: calc(var(--padding) * 2);
	border-bottom: var(--border-bottom);
	height: 45%;
}

.App-footer {
	font-size: var(--gen-font-size);
	padding: var(--padding);
	text-align: center;
	font-style: italic;
}
.Notifications {
    padding: 2em;
    border: 2px solid red;
    border-style: dotted;
    font-family: Arial, Helvetica, sans-serif;
}

ul li[data='default'] {
    color: blue;
}

ul li[data='urgent'] {
    color: red;
}
